.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .2);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    opacity: 0;
    pointer-events: none;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    z-index: 10;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal_result {
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    padding: 10px;
    width: 1100px;
    height: 600px;
    background-color: #f0f0f0;
    border:rgb(51, 51, 51) solid 1px; 
    -webkit-box-shadow: rgba(51, 51, 51, 0.137) 4px 4px; 
            box-shadow: rgba(51, 51, 51, 0.137) 4px 4px;
    -webkit-transform: scale(.5);
        -ms-transform: scale(.5);
            transform: scale(.5);
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.modal_result.active {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}


